___CSS_LOADER_EXPORT___.push([module.id, `::-webkit-scrollbar {
    width: 4px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: none;
    border-radius: 10px;
}

#main-container:hover::-webkit-scrollbar-thumb {
    background: #949aa1;
}
#main-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 1rem 4rem;
    grid-row: 2 / 3;
    grid-column: 2 / 3;
    gap: 10px;
    overflow-y: scroll;
}

.m-content {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 80%;
    min-width: 500px;
}

.m-main-heading-container {
    display: flex;
    width: 100%;
    padding-bottom: 10px;
}

.m-task-container {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 100%;
    margin: 10px 0;
}

.m-main-heading-text {
    font-size: 18px;
    font-weight: 600;
    color: #424242;
}

.task-btn-and-taskform-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 10px 0;
    width: 100%;
}

#add-task-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    background: none;
    gap: 0.2em;
    color: #213555;
    font-weight: 600;
    padding: 8px 10px;
    cursor: pointer;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 4px 1px rgba(66, 66, 66, 0.2);
}

#add-task-btn:hover {
    outline: 2px solid #ACB1D6;
}

.m-add-task-btn-icon {
    width: 1rem;
}`, "",{"version":3,"sources":["webpack://./src/css/content.css"],"names":[],"mappings":"AAAA;IACI,UAAU;AACd;;AAEA,WAAW;AACX;IACI,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,2BAA2B;IAC3B,mBAAmB;IACnB,kBAAkB;IAClB,eAAe;IACf,kBAAkB;IAClB,SAAS;IACT,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,2BAA2B;IAC3B,mBAAmB;IACnB,UAAU;IACV,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,WAAW;IACX,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,uBAAuB;IACvB,eAAe;IACf,WAAW;AACf;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,YAAY;IACZ,gBAAgB;IAChB,UAAU;IACV,cAAc;IACd,gBAAgB;IAChB,iBAAiB;IACjB,eAAe;IACf,sBAAsB;IACtB,mBAAmB;IACnB,6CAA6C;AACjD;;AAEA;IACI,0BAA0B;AAC9B;;AAEA;IACI,WAAW;AACf","sourcesContent":["::-webkit-scrollbar {\n    width: 4px;\n}\n\n/* Handle */\n::-webkit-scrollbar-thumb {\n    background: none;\n    border-radius: 10px;\n}\n\n#main-container:hover::-webkit-scrollbar-thumb {\n    background: #949aa1;\n}\n#main-container {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: center;\n    padding: 1rem 4rem;\n    grid-row: 2 / 3;\n    grid-column: 2 / 3;\n    gap: 10px;\n    overflow-y: scroll;\n}\n\n.m-content {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: center;\n    width: 80%;\n    min-width: 500px;\n}\n\n.m-main-heading-container {\n    display: flex;\n    width: 100%;\n    padding-bottom: 10px;\n}\n\n.m-task-container {\n    display: flex;\n    flex-direction: column;\n    gap: 15px;\n    width: 100%;\n    margin: 10px 0;\n}\n\n.m-main-heading-text {\n    font-size: 18px;\n    font-weight: 600;\n    color: #424242;\n}\n\n.task-btn-and-taskform-container {\n    display: flex;\n    justify-content: center;\n    align-items: flex-start;\n    padding: 10px 0;\n    width: 100%;\n}\n\n#add-task-btn {\n    display: flex;\n    justify