    display: flex;
    justify-content: space-between;
    align-self: center;
    width: 100%;
}

.date-description-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 12px;
    gap: 5px;
}

.task-interface-btn,
.pt-del-btn {
    border: none;
    padding: 4px 6px;
    background: none;
    border-radius: 6px;
}

.task-interface-btn:hover,
.pt-del-btn:hover {
    background-color: #DBDFEA;
}

