    position: relative;

}
body {
    position: relative;
}
.overlay {
    position: absolute;
    background-color: rgba(66, 66, 66, 0.1);
    width: 100vw;
    height: 100vh;
    z-index: 20;
}
.project-form-container, 
.team-form-container {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    top: 35%;
    width: 50%;
    max-width: 600px;
    gap: 10px;
    background-color: #FFF;
    border-radius: 10px;
    box-shadow: 0 0 4px 1px rgba(66, 66, 66, 0.2);
    z-index: 30;
}


.pform-heading-container,
.tform-heading-container {
    display: flex;
    width: 100%;
    border-bottom: 1px solid rgba(66, 66, 66, 0.2);
}

.pform-heading,
.tform-heading {
    display: flex;
    padding: 15px 20px;
    justify-content: flex-start;
    align-items: center;
    font-weight: 900;
}

#pform,
#tform {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
}

.pform-field-container,
.tform-field-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px 20px;
}

#pname,
#tname {
    padding: 8px;
    outline: 1px solid rgba(66, 66, 66, 0.2);
    border-radius: 10px;
    border: none;
    font-size: 14px;
}

#pname:focus,
#tname:focus {
    outline: 2px solid rgba(66, 66, 66, 0.2);
}

.project-form-btn-container,
.team-form-btn-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid rgba(66, 66, 66, 0.2);
    padding: 15px 20px; 
}
.team-form-btn-container {
    justify-content: flex-end !important;
}
.pform-btn-container,
.tform-btn-container {
    display: flex;
    gap: 20px;

}
.pform-btns,
.tform-btns {
    display: flex;
    padding: 8px 15px;
    border-radius: 8px;
    background: none;
    border: none;
    outline: 1px solid rgba(66, 66, 66, 0.2);
    color: #3f3f3f;
}

#pf-cancel:hover,
#tf-cancel:hover  {
    background-color: #DBDFEA;
    outline: none;
    color: #000;
}

#pf-add,
#tf-add {
    background-color: rgba(172, 177, 214, 0.5);
}

#pf-add:hover,
#tf-add:hover {
    background-color: #8294C4;
    color: #FFF;
    outline: none;
}

.team-selector-container {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    padding: 4px;
    outline: 1px solid rgba(66, 66, 66, 0.2);
}

#pform-team-selector {
    border: none;
    background: none;
    outline: none;
}`, "",{"version":3,"sources":["webpack://./src/css/project-form.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;;AAEtB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,kBAAkB;IAClB,uCAAuC;IACvC,YAAY;IACZ,aAAa;IACb,WAAW;AACf;AACA;;IAEI,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,uBAAuB;IACvB,QAAQ;IACR,UAAU;IACV,gBAAgB;IAChB,SAAS;IACT,sBAAsB;IACtB,mBAAmB;IACnB,6CAA6C;IAC7C,WAAW;AACf;;;AAGA;;IAEI,aAAa;IACb,WAAW;IACX,8CAA8C;AAClD;;AAEA;;IAEI,aAAa;IACb,kBAAkB;IAClB,2BAA2B;IAC3B,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,WAAW;AACf;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,kBAAkB;AACtB;;AAEA;;IAEI,YAAY;IACZ,wCAAwC;IACxC,mBAAmB;IACnB,YAAY;IACZ,eAAe;AACnB;;AAEA;;IAEI,wCAAwC;AAC5C;;AAEA;;IAEI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,2CAA2C;IAC3C,kBAAkB;AACtB;AACA;IACI,oCAAoC;AACxC;AACA;;IAEI,aAAa;IACb,SAAS;;AAEb;AACA;;IAEI,aAAa;IACb,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,YAAY;IACZ,wCAAwC;IACxC,cAAc;AAClB;;AAEA;;IAEI,yBAAyB;IACzB,aAAa;IACb,WAAW;AACf;;AAEA;;IAEI,0CAA0C;AAC9C;;AAEA;;IAEI,yBAAyB;IACzB,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,wCAAwC;AAC5C;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,aAAa;AACjB","sourcesContent":[":root {\n    position: relative;\n\n}\nbody {\n    position: relative;\n}\n.overlay {\n    position: absolute;\n    background-color: rgba(66, 66, 66, 0.1);\n    width: 100vw;\n    height: 100vh;\n    z-index: 20;\n}\n.project-form-container, \n.team-form-container {\n    position: absolute;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: flex-start;\n    top: 35%;\n    width: 50%;\n    max-width: 600px;\n    gap: 10px;\n    background-color: #FFF;\n    border-radius: 10px;\n    box-shadow: 0 0 4px 1px rgba(66, 66, 66, 0.2);\n    z-index: 30;\n}\n\n\n.pform-heading-container,\n.tform-heading-container {\n    display: flex;\n    width: 100%;\n    border-bottom: 1px solid rgba(66, 66, 66, 0.2);\n}\n\n.pform-heading,\n.tform-heading {\n    display: flex;\n  